@import '../App.css';
.skills {
    height: auto;
    padding: 5%;
}
h1 {
    font-size: 3em;
}
.skills .container {
    background-color: #fff;
    border-radius: 10px;
    flex-direction: column;
    text-align: center;
    padding: 5%;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.25);
}
.skill-ul {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
}
.skills .black{
    filter: invert();
}
.skill-ul li {
    padding: 1%;
    border-radius: 10px;
    transition: 0.3s all;
}
.skill-ul li:hover {
    -webkit-box-shadow: 0px 5px 0px 0px var(--darker-green);
    -moz-box-shadow: 0px 5px 0px 0px var(--darker-green);
    box-shadow: 0px 5px 0px 0px var(--darker-green);
}